<template>
  <div class="user">
    <h3>{{ msg }}</h3>
    <p>{{ para }}</p>
  </div>
</template>

<script>
  export default {
    name: 'HelloWorld',
    data() {
      return {
        msg: 'a msg',
        para: 'a para',
      };
    }
  };
</script>



